.Button {
    min-width: 120px;
    padding: 14px 27px;
    border-radius: 9999px;
    outline: none;
    font-size: 18px;
    font-family: 'NanumSquareRoundEB';
    cursor: pointer;
  }

.Button.playButton {
    border: solid 1px #7090ff;
    color: #7090ff;
    background-color: rgba(0, 89, 255, 0.2);
}

.Button.playButton:hover {
    background-color: rgba(0, 89, 255, 0.5);
}

.Button.resetButton {
    border: solid 1px #ff4664;
    color: #ff4664;
    background-color: rgba(255, 78, 78, 0.2);
}

.Button.resetButton:hover {
    background-color: rgba(255, 78, 78, 0.4);
}